import {Component, ElementRef, Inject, Renderer2, ViewEncapsulation, OnInit} from '@angular/core';
import {Localization} from "vebto-client/core/types/models/Localization";
import {Localizations} from "vebto-client/translations/localizations.service";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";

@Component({
    selector: 'crupdate-localization-modal',
    templateUrl: './crupdate-localization-modal.component.html',
    encapsulation: ViewEncapsulation.None,
})
export class CrupdateLocalizationModalComponent implements OnInit {

    /**
     * Localization model.
     */
    public localization: Localization = new Localization();

    /**
     * Whether localization creation is currently in progress.
     */
    public loading = false;

    /**
     * Errors returned from backend.
     */
    public errors: any = {};

    /**
     * CrupdateLocalizationModalComponent Constructor.
     */
    constructor(
        private localizations: Localizations,
        private dialogRef: MatDialogRef<CrupdateLocalizationModalComponent>,
        @Inject(MAT_DIALOG_DATA) public data: {localization?: Localization},
    ) {}

    ngOnInit() {
        if (this.data.localization) {
            this.localization = this.data.localization;
        }
    }

    /**
     * Create new localization or update existing one.
     */
    public confirm() {
        this.loading = true;

        let request = this.localization.id ? this.updateLocalization() : this.createNewLocalization();

        request.subscribe(lang => {
            this.loading = false;
            this.close(lang);
        }, errors => {
            this.loading = false;
            this.handleErrors(errors);
        });
    }

    /**
     * Close the modal and pass specified data.
     */
    public close(data?) {
        this.dialogRef.close(data);
    }

    /**
     * Create a new localization.
     */
    public createNewLocalization() {
        return this.localizations.create(this.getPayload());
    }

    /**
     * Update existing localization.
     */
    public updateLocalization() {
        return this.localizations.update(this.localization.id, this.getPayload());
    }

    /**
     * Get payload for creating/updating localization.
     */
    private getPayload() {
        return {name: this.localization.name};
    }

    /**
     * Format errors received from server for display.
     */
    public handleErrors(response?: {messages: object}) {
        //clear old errors if no response is specified
        if ( ! response) return this.errors = {};

        this.errors = response.messages;
        this.loading = false;
    }
}


