import {RouterModule, Routes} from '@angular/router';
import {NgModule} from "@angular/core";
import {AppearanceComponent} from "./appearance.component";
import {CanDeactivateAppearance} from "./can-deactivate-appearance.guard";
import {AuthGuard} from "../../guards/auth-guard.service";
import {AppearanceEditorResolver} from "vebto-client/admin/appearance/appearance-editor-resolver.service";
import {ProjectsResolver} from "../../../../src/app/dashboard/projects-resolver.service";
import {DashboardComponent} from "../../../../src/app/dashboard/dashboard.component";
import {UsersComponent} from "../users/users.component";

const routes: Routes = [
    {
        path: 'appearance',
        component: AppearanceComponent,
        data: {permissions: ['appearance.update']},
        resolve: {defaultSettings: AppearanceEditorResolver},
        canActivate: [AuthGuard],
        canDeactivate: [CanDeactivateAppearance],
        children: [

        ]

    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AppearanceRoutingModule {}


